import React from "react";
import { Box, Heading, SimpleGrid } from "@chakra-ui/react";
import headlineExperiencesData from "../assets/json/headlineExperiencesData.json";
import { ExperienceItem } from "../types/portfolioData";
import ExperienceCard from "./ExperienceCard";

import ViewMoreButton from "./ViewMoreButton";

const Experience: React.FC = () => {
  const experienceItems: ExperienceItem[] = headlineExperiencesData;
  return (
    <Box py={10} textAlign={{ base: "center", sm: "left" }}>
      <Heading size="md" mb={6}>
        
      </Heading>
      <SimpleGrid
        justifyItems="center"
        columns={{ base: 1, sm: 2, xl: 3 }}
        spacing={5}
      >
        {experienceItems.map((exp) => (
          <ExperienceCard Exp={exp}></ExperienceCard>
        ))}
      </SimpleGrid>
    </Box>
  );
};

export default Experience;
