import React, { useEffect } from "react";
import {
  Box,
  Image,
  Modal,
  ModalOverlay,
  ModalContent,
  ScaleFade,
  Text,
  useDisclosure,
} from "@chakra-ui/react";
import { customCSS } from "../styles/customCSS";
import { ArtItem } from "../types/portfolioData";

interface ImageModalProps {
  Art: ArtItem;
}

const ImageModal: React.FC<ImageModalProps> = ({ Art }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();

  useEffect(() => {
    if (isOpen) {
      document.body.style.setProperty("margin-right", "0px", "important");
    }
  }, [isOpen]);

  return (
    <ScaleFade initialScale={0.5} in={true}>
      <Box>
        <Image
          src={process.env.PUBLIC_URL + "/images/arts/" + Art.image}
          alt={Art.title}
          onClick={onOpen}
          sx={customCSS.artImageStyle}
          _hover={customCSS.hoverEffectStyle}
        />
        <Modal isOpen={isOpen} onClose={onClose} isCentered>
          <ModalOverlay />
          <ModalContent sx={customCSS.modalStyle}>
            <Image
              sx={customCSS.modalImageStyle}
              src={process.env.PUBLIC_URL + "/images/arts/" + Art.image}
              alt={Art.title}
              roundedTop={3}
            />
            <Text textAlign="center" p={5} fontWeight="bold">
              {Art.title}
            </Text>
          </ModalContent>
        </Modal>
      </Box>
    </ScaleFade>
  );
};

export default ImageModal;
