import React from "react";
import {
  Card,
  Image,
  Text,
  ScaleFade,
  CardFooter,
  Stack,
  Badge,
  CardBody,
} from "@chakra-ui/react";
import { ExperienceItem } from "../types/portfolioData";
import { customCSS } from "../styles/customCSS";
import { Helpers } from "../helpers/DataHelpers";

interface ExperienceCardProps {
  Exp: ExperienceItem;
}

const ExperienceCard: React.FC<ExperienceCardProps> = ({ Exp }) => {
  return (
    <ScaleFade initialScale={0.5} in={true}>
      <Card sx={customCSS.cardStyle}>
        <Image
          src={process.env.PUBLIC_URL + Exp.image}
          alt={Exp.title}
          mb={0}
          w={150}
          h={150}
          my={1}
          mx="auto"
        />
        <CardBody pt={0}>
          <Text fontSize="sm" fontWeight="bold" textAlign="center">
            {Exp.title}
          </Text>
          <Text fontSize="xs" mt={2} whiteSpace="pre-line">
            {Helpers.convertAtToNewline(Exp.description)}
          </Text>
        </CardBody>
        <CardFooter pt={0}>
          <Stack direction="row" sx={{ flexWrap: "nowrap" }}>
            {Exp.language.map((language, index) => (
              <Badge key={index} sx={customCSS.cardBadgeStyle}>
                {language}
              </Badge>
            ))}
          </Stack>
        </CardFooter>
      </Card>
    </ScaleFade>
  );
};

export default ExperienceCard;
